import React from "react";
import { useMsal } from "@azure/msal-react";
import { loginRequest } from "../authConfig";
import DropdownButton from "react-bootstrap/DropdownButton";
import Dropdown from "react-bootstrap/esm/Dropdown";

/**
 * Renders a drop down button with child buttons for logging in with a popup or redirect
 */
export const SignInButton = () => {
    const { instance } = useMsal();

    const handleLogin = (loginType) => {
        if (loginType === "popup") {
            instance.loginPopup(loginRequest).catch(e => {
                console.error(e);
            });
        } else if (loginType === "redirect") {
            instance.loginRedirect(loginRequest).catch(e => {
                console.error(e);
            });
        }
    }
    return (
        <DropdownButton variant="secondary" className="ml-auto" drop="left" title="로그인">
            <Dropdown.Item as="button" onClick={() => handleLogin("popup")}>팝업</Dropdown.Item>
            <Dropdown.Item as="button" onClick={() => handleLogin("redirect")}>리다이렉트</Dropdown.Item>
        </DropdownButton>
    )
}