import React, { useState } from "react";
import { AuthenticatedTemplate, UnauthenticatedTemplate, useMsal } from "@azure/msal-react";
import { loginRequest } from "./authConfig";
import { PageLayout } from "./components/PageLayout";
import { ProfileData } from "./components/ProfileData";
import { callMsGraph } from "./graph";
import { callMsGraphTenant } from "./graph";
import Button from "react-bootstrap/Button";
import "./styles/App.css";

/**
 * Renders information about the signed-in user or a button to retrieve data about the user
 */
const ProfileContent = () => {
    const { instance, accounts } = useMsal();
    const [graphData, setGraphData] = useState(null);
    const [graphDataTenant, setGraphDataTenant] = useState(null);

    function RequestProfileData() {
        instance.acquireTokenSilent({
            ...loginRequest,
            account: accounts[0]
        }).then((response) => {
            callMsGraph(response.accessToken).then(response => setGraphData(response));
        });
    }
    function RequestProfileDataTenant() {
        instance.acquireTokenSilent({
            ...loginRequest,
            account: accounts[0]
        }).then((response) => {
            callMsGraphTenant(response.accessToken).then(response => setGraphDataTenant(response));
        });
    }

    return (
        <>
            <h5 className="card-title">환영합니다. {accounts[0].name} 님!</h5>
            <div>
            {graphData 
            ? <ul>
                <li style={{listStyleType: "none"}}>DisplayName: {graphData.displayName}</li>
                <li style={{listStyleType: "none"}}>SurName: {graphData.surname}</li>
                <li style={{listStyleType: "none"}}>GivenName: {graphData.givenName}</li>
                <li style={{listStyleType: "none"}}>Mail: {graphData.mail}</li>
                    <li style={{listStyleType: "none"}}>id: {graphData.id}</li>
                </ul>
                :<Button variant="secondary" onClick={RequestProfileData}>내 계정 정보 보기</Button>
            }
            </div>
            <br/>
            <div>
                {graphDataTenant
                ?<ul>
                    <li style={{listStyleType: "none"}}>이름: {graphDataTenant.tenantId}</li>
                    <li style={{listStyleType: "none"}}>테넌트 ID: </li>
                    <li style={{listStyleType: "none"}}>주 도메인: </li>
                </ul>
                :<Button variant="secondary" onClick={RequestProfileDataTenant}>내 회사 정보 보기</Button>
                }
            </div>
        </>
    );
};


const MainContent = () => {    
    return (
        <div className="App">
            <AuthenticatedTemplate>
                <ProfileContent />
            </AuthenticatedTemplate>

            <UnauthenticatedTemplate>
                <h5 style={{fontFamily:"nanusquare", fontWeight:600, marginTop:"12%", fontSize:"15px",color:"gray"}}className="card-title">로그인을 완료해 주세요!</h5>
            </UnauthenticatedTemplate>
        </div>
    );
};

export default function App() {
    return (
        <PageLayout>
            <MainContent />
        </PageLayout>
    );
}
